<template>
  <div class="pageContent">
    <div class="pageHead">快递发货功能</div>
    <el-card class="box-card">
      <span>计费方式：</span>
      <el-radio @change="changeRadio" :disabled="radioLoading" v-model="radioForm.freightRule" label="0">组合运费</el-radio>
      <el-radio @change="changeRadio" :disabled="radioLoading" v-model="radioForm.freightRule" label="1">累加运费</el-radio>
      <el-button style="margin-left:40px;" @click="showRules = true">运费计费规则</el-button>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin:20px 0;" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="$router.push({path:'/logistics/logisticsTemplate/newTemplate'})">新建运费模板</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card class="box-card table">
        <el-table
          :data="list"
          style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-table :data="props.row.freightTemplateRuleDtoList" style="width: 100%">
                <el-table-column label="可配送区域">
                  <template slot-scope="scope">
                    {{scope.row.distributionAreaName}}
                  </template>
                </el-table-column>
                <el-table-column label="首件（个）">
                  <template slot-scope="scope">
                    {{scope.row.firstPiece}}
                  </template>
                </el-table-column>
                <el-table-column label="运费（元）">
                  <template slot-scope="scope">
                    {{scope.row.firstFreightPrice}}
                  </template>
                </el-table-column>
                <el-table-column label="续件（个）">
                  <template slot-scope="scope">
                    {{scope.row.addPiece}}
                  </template>
                </el-table-column>
                <el-table-column label="续费（元）">
                  <template slot-scope="scope">
                    {{scope.row.addFreightPrice}}
                  </template>
                </el-table-column>
              </el-table>
            </template>
          </el-table-column>
          <el-table-column label="运费模板名称" prop="templateName"></el-table-column>
          <el-table-column label="使用此模板的商品个数" prop="productCount"></el-table-column>
          <el-table-column label="最后编辑时间" prop="updateTime"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button v-if="scope.row.isDefault == 0" type="text" size="small" style="color:#E6A23C;">默认</el-button>
              <el-button v-if="scope.row.isDefault == 1" type="text" size="small" @click="setDefault(scope.row)">设为默认</el-button>
              <el-button type="text" size="small" @click="$router.push({path:'/logistics/logisticsTemplate/editTemplate',query:{templateId:scope.row.id}})">编辑</el-button>
              <el-button type="text" size="small" @click="deleteItem(scope.row)" style="color:#909399;">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <div style="padding:20px 0 0 0 ;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="listForm.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </div>
    <!-- 规则弹出框 -->
    <rules @closeRules="closeRules" :rulesShow.sync="showRules"></rules>
  </div>
</template>
<script>
import rules from './components/rules';
import { mapState } from 'vuex'
import { 
  getShopConfigInfo,
  updateShopConfigInfo,
  getFreightTemplateInfoList,
  updateDefault,
  deleteFreightTemplateInfo
} from '@/api/logistics/logisticsTemplate'
export default {
  data(){
    return{
      showRules:false,          // 规则弹出框
      radioForm:{
        freightRule:'0'
      },
      radioLoading:false,  
      listForm:{
        pageNum:1,
        pageSize:5
      },
      listTotal:0,
      list:[],  
    }
  },
  created(){
    this.radioForm.shopId = this.shop.shopId
    this.listForm.shopId = this.shop.shopId
    this.getStatus();
    this.getData()
  },
  methods:{
    // 
    getData(){
      getFreightTemplateInfoList(this.listForm).then(res=>{
        console.log('数据列表',res)
        this.listTotal = res.data.total
        this.list = res.data.list
      })
    },
    // 列表 - 删除模板
    deleteItem(e){
      if(e.productCount != 0){
        this.$alert('该模板有'+e.productCount+'个商品在使用，如需删除请先更换这些商品的运费模板', '提示', {
          confirmButtonText: '确定',
          type: 'warning',
          callback: action => {}
        });
      }else{
        this.$confirm('删除运费模板【'+e.templateName+'】?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteFreightTemplateInfo({templateId:e.id}).then(res=>{
            if(res.code == 200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.getData();
            }else{
              this.$message.error(res.message);
            }
          })
        }).catch(() => {});
      }
    },
    // 列表 - 设为默认模板
    setDefault(e){
      console.log(e)
      this.$confirm('将此条设置为默认模板?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(() => {
        updateDefault({shopId:this.shop.shopId,templateId:e.id}).then(res=>{
          if(res.code == 200){
            this.$message({
              message: '设置成功',
              type: 'success'
            });
            this.getData();
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 修改店铺计费方式
    changeRadio(){
      this.radioLoading = true;
      console.log('this.radioForm',this.radioForm)
      updateShopConfigInfo(this.radioForm).then(res=>{
        console.log('更改计费方式',res)
        if(res.code == 200){
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.radioLoading = false;
        }else{
          this.radioLoading = false;
          this.$message.error(res.message);
        }
      })
      
    },
    // 获取店铺计费方式状态
    getStatus(){
      getShopConfigInfo(this.shop.shopId).then(res=>{
        console.log('获取店铺计费方式',res)
        this.radioForm.freightRule = ''+res.data.freightRule
      })
    },
    // 列表分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.listForm.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.listForm.pageNum = val;
      this.getData()
    },
    // 规则弹出框
    closeRules(e){
      console.log(e)
      this.showRules = false
    },
  },
  components: {
    rules,
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style>
  .table /deep/ .expanded{
    background-color: #fff;
  }
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>