<template>
  <div>
    <el-dialog 
      :close-on-press-escape="close"
      :close-on-click-modal="close"
      :show-close="close"
      :visible.sync="rulesShow" center title="运费模板" width="70%">
      <div>
        <div style="margin-bottom:20px;text-align:center;font-size:16px;">选择相应的运费模板，即可指定该商品，可配送至相应区域，并按规则收取相应运费,包括：累加运费和组合运费两种规则</div>
        <el-row type="flex" class="row-bg" justify="space-around">
          <el-col :span="11">
            <el-card shadow="hover">
              <p style="margin-bottom:20px;">
                累加运费：分别计算出来商品使用模板的运费最大值，进行累加。不计算包邮商品的运费
              </p>
              <p style="margin-bottom:10px;">
                1.不同或相同的商品，设置同一运费模板：按该模板设置的规则计算，不足续件或者续重的数目的时候，仍然按照续件或者续重的数目进行计算。例：商品A，B都是用模板X（首件10元，续2件5元），如果购买商品A和B，各一件，则一共购买两件，运费=10+5=15元。购买A一件，B两件，则一共购买三件，运费=10+5=15元。
              </p>
              <p>
                2.不同的商品，设置不同的运费模板：分别计算每个运费模板规则应收运费，再累加计算合计运费；例如： 例如商品A（1件）使用用模板X（首1件，10元，续2件，5元），商品B（1件）使用模板Z（首2件，12元，续1件，5元），如果购买商品A和B各2件，则运费=模板X的运费+模板Z的运费=（10+5）+（12）=27元。
              </p>
            </el-card>
          </el-col>
          <el-col :span="11">
            <el-card shadow="hover">
              <p>组合运费：</p>
              <p>1)计算使用运费模板的运费价格x：取所有商品中，首件/首重金额最大的运费模板，计算使用该模板的所有商品运费；使用其他模板的所有商品都按照该商品所试用的续件/续费金额来计算；最后再求和</p>
              <p>2）计算使用统一运费的商品运费y：取最大的统一运费。</p>
              <p>3）比较x和y，运费=x和y的较大值。</p>
              <p style="margin-bottom:20px;">4）不计算包邮商品的运费</p>
              <p>例如：</p>
              <p style="margin-bottom:10px;">1.不同或相同的商品，设置同一运费模板：同按商品累加运费的计算方式，按该模板设置的规则计算。例：商品A，B都是用模板M（首件10元，续2件5元），如果购买商品A和B，各一件，则一共购买两件，运费=10+5=15元。</p>
              <p>2.不同的商品，设置不同的运费模板：不同于按照商品累加的计算方式。例： 例如商品A（1件）使用用模板M（首1件，10元，续1件，5元），商品B使用模板N（首2件，12元，续1件，5元），如果购买商品A和B，各2件，则运费=模板N的运费（首费为12，大于模板M的10）+模板M的运费（按照续费计算）=（12）+（5*2）=22元。如果按商品累加计费，则运费=模板M的运费+模板N的运费=（10+5）+（12）=27元。</p>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="click">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: {
    rulesShow: {
      type: Boolean,
      default: false
    }
  },
  data(){
    return{
      close:false
    }
  },
  methods:{
    click() {
      this.$emit('closeRules', true);
    }
  }
}
</script>